const History = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			История Школы
		</PanelHeader>
		<div align="center">
		<h4>Школа-страна, школа-судьба,
Магия цифр пять и восемь. 
В миг обратит жизнь нашу вспять, 
в детство вернет нас опять!
(сл. Т.А.Потапова)
</h4>

<h4>Муниципальное бюджетное общеобразовательное учреждение «Средняя школа № 58» г. Иваново на ул. Дунаева, д. 13 - типовой проект 272 (2-02-02) (средняя школа на 880 мест, архитекторы Асс Л. Е., Гринцберг А. С.). В первые годы работы в школе училось 1055 детей и работало 48 учителей. (В Государственном архиве Ивановской области хранится отчёт о партийной проверке школы в 1958 году. В этом документе находятся характеристики на школу, учителей, описание уроков и мероприятий).</h4>
<h3>Теперь переходим к основной истории образовательной организации.
История школы</h3>

<h4>
 1 сентября 1953 года школа гостеприимно распахнула двери своим ученикам, но официальной датой открытия является 7 января 1954 года (до 7 января 1954 года 58 школа именовалась – «Дунаевская»), несмотря на то, что в ГАИО представлены сведения о том, что здание школы было введено в эксплуатацию в феврале 1955 года, в школе хранился приказ об открытии учреждения 7 января 1954 года, в 2000-е годы приказ был утерян, но на лестнице, ведущей на 3 этаж, осталась высечена дата «1954 год», а на первой ступени лестницы (рядом со столовой) была дата открытия школы 1953 год (но после капитального ремонта надпись на первой ступени не сохранилась). Все эти даты очень спорны, так как школу открыли 1 сентября 1953 года, но официально школа была открыта 7 января 1954 года. (Документально эти факты не подтверждены, но все выпускники с уверенностью говорят о правдивости и точности этих дат) 
</h4>
<img className="kan" src={h1} alt="h1"/>
<h4>1 сентября 1953 года четырехэтажное здание с большими рекреациями и просторными кабинетами заполнилось детским смехом,любопытными взглядами и активной работой старшеклассников.
Первый свой выпуск школа сделала уже в 1956 году. 
</h4>
<img className="kan" src={h2} alt="h2"/>
<h4>Первым директором, вложившим немало сил и энергии в становление школы, был Заслуженный учитель РСФСР Рыскин Арсений Федорович (преподаватель истории, директор до 1973 года).</h4>
		<img className="kan" src={h3} alt="h3"/>
<h4>Именно при нем, вместе с первым составом администрации начал складываться педагогический коллектив.
Школа росла, развивалась, вносила свой весомый вклад в образование района и города. Вместе с ней рос и коллектив школы, его профессиональный и научный потенциал. 
В 1958 году в школе было открыто более 20 кружков, секций и курсов для учащихся и педагогов. </h4>
<img className="kan" src={h4} alt="h4"/>
<h4>
4 декабря 1964 года в городской газете «Рабочий край» была опубликована статья о нашей школе:
 «В ивановской школе № 58 можно часто увидеть учеников с
  повязкой на руке. На ней - эмблема Красного Креста с буквами «КЮМ». 
 Это клуб юных медиков. В школе члены клуба проводят викторины на гигиенические 
 темы, ввели в правило, чтобы все октябрята мыли руки перед завтраком, 
 имели полотенца и сменную обувь».
К сожалению, материалов в периодической печати о школе не так много,
 возможно, это связанно с тем, что школа располагалась в районе, 
 где жили партийные работники, чьи дети и обучались в нашей школе. 
</h4>
<img className="kan" src={h5} alt="h5"/>
<h4>
 У образовательной организации не было конкуренции (школа 1, 4, 23.).
  Многие вспоминают, что детей было очень трудно записать в первый 
  класс (школа была очень «элитной»), очереди выстраивались 
  за несколько суток до начала записи.
</h4>
<img className="kan" src={h6} alt="h6"/>
<h4 >
Следует отметить тот факт, что Арсений Фёдорович жил прямо в школе
вместе со своей семьей, его квартира располагалась на 1-м этаже, где
сейчас находится мастерская для мальчиков, кабинет был рядом (сейчас
там кабинет заместителя директора по АХЧ). (В 80-е годы там
располагалась «красная комната», где хранились альбомы с фотографиями,
летопись школы, книги почётных гостей, вымпелы, знамёна, НО, в 
1990-е и в начале 2000-х гг. ситуация в стране была тяжёлая, многое 
беспощадно уничтожалось и выкидывалось, что в дальнейшем и привело кпереписыванию истории). 
 Во дворе школы, располагался гараж директора и будка для собаки, был 
 яблоневый сад и небольшой огород.25 февраля 2020 года в школе открыли 
 мемориальную доску директору А.Ф.Рыскину
</h4>
<img className="kan" src={h7} alt="h7"/>
<br></br>
<h4>В 1973 году директором школы стала Лапшова Тамара Александровна.</h4>
<img className="kan" src={h8} alt="h8"/>
<h4>Всех регалий не перечесть: благодарности, грамоты, кубки…. Учащиеся 58-ой всегда в первых рядах. 
(На 3-м этаже от кабинета № 34 до кабинета №35 стоял стеллаж во все стену, где и находились все кубки, но после 90-х достижения школы пропали).</h4>
<img className="kan" src={h9} alt="h9"/>
<h4>В школе создаются трудовые отряды из «трудных» мальчишек и девчонок, которые работали в сфере обслуживания и на сельскохозяйственных работах. За общественно-полезную работу учащихся в микрорайонах педагогам была вручена грамота Октябрьского райкома комсомола.
В школе начала работать дружина им. Аркадия Гайдара.
В октябре 1975 года из учащихся первых классов набирается группа ребят, получившая в дальнейшем название «А+Б» (педагог Охомуш Т.). 
</h4>
<img className="kan" src={h10} alt="h10"/>
<h4>
В 1980-е годы разрабатывается проект о строительстве второго здания, но начавшаяся «перестройка» не позволила ему сбыться. 
В 1987 году Тамару Александровну сменил Шкилёв Александр Николаевич, а в 1989 году Лебедев Борис Иванович (до 1994 года) (ремонт фасада здания). В 1994 – 1996 годах школой руководил Соколов Андрей Владимирович, которого сменила Кузьмина Татьяна Борисовна. 
</h4>
<img className="kan" src={h12} alt="h12"/>
<h4>
Жизнь закипела с новой силой. Школа искала новых друзей, новые направления работы. В 1998 году стала одной из немногих ивановских школ, изучающих немецкий язык. 
</h4>
<img className="kan" src={h13} alt="h13"/>
<h4>
В школу стали приезжать граждане ГДР, наши ребята и педагоги побывали за границей с ответными визитами. Завязались теплые дружеские отношения с «Обществом друзей Германии», которые поддерживаются до сегодняшнего дня. 
В 2009 году школой стала руководить Усольцева Ольга Анатольевна. 
</h4>
<img className="kan" src={h14} alt="h14"/>
<h4>А с 15 февраля 2012 года и до 31 июля 2019 Слепова Валентина Владимировна. </h4>
<img className="kan" src={h15} alt="h15"/>
<h4>После длительного «застоя», тяжелых 1990-х, когда школа потеряла свой статус, свою репутацию в городе (всего около 200-т обучающихся) Валентина Владимировна смогла поднять школу «с колен», вывела её на новый уровень, начала модернизацию учреждения, провела капитальный ремонт кровли, фундамента, перекрытий, рекреаций, столовой, библиотеки, гардероба, компьютерных классов, оконных блок (самая первая школа в городе, где полностью все деревянные окна были заменены на пластиковые) и др. 
</h4>
<img className="kan" src={h16} alt="h16"/>
<h4>В школе началась новая жизнь, в 2012 году создаётся Ученический Совет, который сейчас является одним из лучших в нашем городе.(председатели Виктория Иконг 2014-2015, Игнатий Куликов 2015-2017, Виктор Иконг 2017-2018, Елизавета Огурцова 2018-2020). </h4>
<img className="kan" src={h16} alt="h17"/>
<h4>Педагогический коллектив стал увеличиваться, приходят молодые педагоги, в том числе и выпускники образовательной организации.   </h4>
<img className="kan" src={h18} alt="h18"/>
<h4> Школа вышла на новую ступень после длительного перерыва, победы на конкурсах разных уровней дали толчок к повышению имиджа школы. </h4>
<h4> В учреждение начали приезжать VIP-гости, визит которых был не только рабочий, но и с целью просто пообщаться с учениками и педагогами, поделиться своим опытом и знаниями, 1 марта 2013, 2014, 2015,гг. – рабочие визиты и посещение школьных мероприятий О.А. Хасбулатовой (выпускница школы), А.С. Кузьмичёва, Е.А. Юферовой (начальник УО до 2019г.), 2017, 2019 г. – встреча Ученического Совета школы с Н.В. Курочкиной (депутат) (Наталья Викторовна посещает все мероприятия школы как гость, и как организатор), 2018 г. – рабочий визит С.С. Воскресенского (губернатор), 2018 – встреча Ученического Совета с Б.Ю. Мурванидзе (лидер молодёжной организации), декабрь 2018 г. – рабочий визит и встреча УС с мэром г. Иваново В.Н. Шарыповым. </h4>
<img className="kan" src={h19} alt="h19"/>
<h4>С 21 августа 2019 года директором школы становится Ситнова Анастасия Андреевна.  </h4>
<img className="kan" src={h20} alt="h20"/>
<h4> </h4>
		</div>
	</Panel>
);

